import React, { Component } from "react";
import Navbar from "../components/Navbar";
import Image from "../components/Image";

class Index extends Component {
  constructor(props) {
    super(props);
    this.state = {
      articles: [],
      images: [],
      content: [],
      isLoaded: false
    };
  }

  componentDidMount() {
    fetch("https://interview-project-17987.herokuapp.com/api/article")
      .then(res => res.json())
      .then(json => {
        this.setState({
          isLoaded: true,
          articles: json.map(article => {
            const updated = { ...article, id: count };
            let count = count + 1;
            return updated;
          }),
          images: json.map(article => {
            const myImages = { images: article.images };
            return myImages;
          }),
          content: json.map(article => {
            const myContent = { content: article.content };
            return myContent;
          })
        });
      });
  }

  handleUpdateImage = () => {
    console.log("Event Handler Called");
  };

  render() {
    console.log(this.state.articles);
    console.log(this.state.images);
    console.log(this.state.content);
    const { isLoaded, articles } = this.state;
    if (!isLoaded) {
      return <div> Loading... </div>;
    } else {
      return (
        <article>
          <Navbar />
          <Image title={articles[0]} onUpdateImg={this.handleUpdateImage} />
        </article>
      );
    }
  }
}

export default Index;
